/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
  border: 0; }

* {
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote::before, blockquote::after {
  content: '';
  content: none; }

q::before, q::after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.truncate {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
  .truncate_title {
    height: 4em; }
  .truncate_subtitle {
    width: 7em; }
  .truncate_text {
    width: 20.43em; }

.title {
  font-weight: 400;
  font-size: 3.0625em;
  line-height: 1.28571;
  color: #fff;
  font-family: 'PT Sans', sans-serif; }

.subtitle {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 1.18182;
  color: #111; }

.text {
  font-weight: 300;
  font-size: 0.875em;
  line-height: 1.14286;
  color: #111; }

.page-signup {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding: 20px;
  background-color: #f4f5fc; }

.form {
  font-size: 1rem;
  display: flex;
  width: 100%;
  max-width: 665px;
  height: 400px;
  max-height: 600px;
  box-shadow: 0 12px 82px rgba(0, 0, 0, 0.2); }
  .form__title {
    position: relative;
    width: 20.6%;
    min-width: 70px;
    height: 100%;
    padding-top: 20px;
    padding-left: 6px;
    background-color: #5a61ed;
    transform: scale(-1);
    writing-mode: vertical-lr; }
  .form__data-entry {
    width: 79.4%;
    padding: 38px 22px 32px 20px;
    font-family: 'Work Sans', sans-serif;
    background-color: #fff; }
  .form__subtitle {
    margin-bottom: 5px; }
  .form__text {
    margin-bottom: 20px; }
